#logo,
.logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 65vw;
  height: auto;
  margin-top: -30vw;
}
@media (min-width: 800px) {
  #logo,
  .logo {
    width: 65vh;
    top: 14vh;
    transform: translateX(-50%);
    margin-top: 0;
  }
}

.button {
  position: absolute;
  display: block;
  text-align: center;
  font: 2.5em/1em Arial, sans-serif;
  color: #ffe5e5;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
  background: #e5e5e5;
  width: 60vw;
  height: 60vw;
  top: 58%;
  left: 50%;
  margin: -30vw 0 0 -30vw;
  border-radius: 50%;
  box-shadow: inset 0 -5px 10px rgba(0, 0, 0, 0.5), inset 0 15px 10px rgba(255, 255, 255, 0.5), 0 5px 10px rgba(0, 0, 0, 0.75), 0 10px 10px rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  padding: 78px 9px 0 0;
  cursor: pointer;
  transition: all 0.1s ease;
}
@media (min-width: 800px) {
  .button {
    width: 50vh;
    height: 50vh;
    margin: -25vh 0 0 -25vh;
  }
}
.button:before {
  content: "";
  display: block;
  position: absolute;
  z-index: -5;
  width: 66vw;
  height: 66vw;
  top: 58%;
  left: 50%;
  border-radius: 50%;
  background: #e5e5e5;
  top: -3vw;
  left: -3vw;
  box-shadow: inset 0 7px 15px rgba(255, 255, 255, 0.5), inset 0 -7px 15px rgba(0, 0, 0, 0.5);
  border: 2px solid #444;
}
@media (min-width: 800px) {
  .button:before {
    width: 56vh;
    height: 56vh;
    top: -3vh;
    left: -3vh;
  }
}
.button:active {
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.5), 0 5px 10px rgba(0, 0, 0, 0.35), 0 10px 10px rgba(0, 0, 0, 0.35);
  color: #ffeded;
}

.airhorns {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 55px;
  background: #111;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  gap: 15px;
  align-items: center;
  align-content: center;
}
.airhorns label {
  color: #fff;
  outline: none;
  cursor: pointer;
}
.airhorns label span {
  display: inline-block;
  padding: 10px 15px 5px 15px;
  border-radius: 4px;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 600;
  border: 2px solid transparent;
  font-family: helvetica;
  line-height: 1;
}
.airhorns label input {
  position: absolute;
  left: -9999px;
  height: 0;
  width: 0;
  opacity: 0;
}
.airhorns label input:checked + span {
  border: 2px solid #fff;
}